<template>
  <div class="index">
    <el-row>
      <el-row class="topPic">
        <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeInRight"
            leave-active-class="animated fadeInLeft">
          <el-row v-show="isShowAuthor">
            <el-row class="minePic" @click.native="goHome"></el-row>
            <el-row class="myName mt-15 font-size--large">Mr.wang</el-row>
            <el-row class="sub_text">别看不起小事，每一段艰难都是磨练。</el-row>
          </el-row>
        </transition>
      </el-row>
      <el-row class="main text-center">
        <span class="main_text"> Every day is a good day </span>
      </el-row>
      <el-row class="three-link">
        <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeInRight"
            leave-active-class="animated fadeInLeft">
          <el-row v-show="isShowLink" type="flex" justify="space-around">
            <el-row class="icon1">
              <el-image class="icon-pic icon-pic1" src="img/author/QQ.jpg"></el-image>
              <icon-svg
                  style="height: 80px;width: 80px"
                  name="qq"/>
            </el-row>
            <el-row class="icon2">
              <el-image class="icon-pic icon-pic2" src="img/author/weChat.jpg"></el-image>
              <icon-svg
                  style="height: 80px;width: 80px"
                  name="wechat"/>
            </el-row>
          </el-row>
        </transition>
      </el-row>
    </el-row>
  </div>
</template>

<script>
// 滚动监听
//https://www.npmjs.com/package/in-view
import inView from 'in-view'
import util from '@/libs/util.js'

export default {
  name: 'index',
  data () {
    return {
      isCollapse: true,
      isShowAuthor: true,
      isShowLink: false,
    }
  },
  created () {
    let a = window.location.href.split(':')[0]
    util.cookies.set(a + '-token', Math.random()*10)
  },
  mounted () {
    inView('.topPic')
      .on('enter', () => {
        this.isShowAuthor = true
      })
      .on('exit', () => {
        this.isShowAuthor = false
      })
    inView('.three-link')
      .on('enter', () => {
        this.isShowLink = true
      })
      .on('exit', () => {
        this.isShowLink = false
      })
  },
  methods: {
    goHome () {
      this.$router.push({name: 'home'})
    },
  },

}
</script>
<style lang="scss">
.index {
  width: 100%;
  overflow-x: hidden;
  background: #f8f8f8;

  .topPic {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-image: url("https://api.dujin.org/bing/1366.php");
    background-size: cover;
    background-position: center;

    .minePic {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      overflow: hidden;
      cursor: pointer;
      background-image: url("~@/../public/img/author/author.jpg");
      background-size: cover;
      background-position: center;
    }
    .myName {
      color: #fff;
      font-size: 60px;
      font-weight: 600;
      letter-spacing: 3px;
    }
    .sub_text {
      font-size: 21px;
      font-weight: 300;
      color: #fff;
      letter-spacing: 6px;
      margin-top: .5em;
      margin-bottom: .5em;
    }
  }

  .main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    /*background-image: url("https://api.dujin.org/pic/");*/
    background-color: #3097ee;
    background-size: cover;
    background-position: center;
    padding: 20px 0;

    .main_text {
      text-transform: capitalize;
      font-size: 50px;
      font-weight: 600;
      font-family: Arial, Helvetica, sans-serif;
      color: rgba(174, 51, 226, 0.976);
    }
  }

  .three-link {
    padding: 120px 0;

    .icon-pic {
      width: 300px;
      height: 200px;
    }

    .icon1, .icon2 {
      position: relative;
    }

    .icon-pic1, .icon-pic2 {
      top: -160px;
      left: -25px;
      position: absolute;
      display: none;
    }

    .icon1:hover {
      .icon-pic1 {
        display: block;
      }
    }

    .icon2:hover {
      .icon-pic2 {
        display: block;
      }
    }
  }
}
</style>
